import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/mbaas/core/service/authentication/auth.service';
import { CriptoService } from 'src/mbaas/core/service/encryption/cripto.service';
import { SendInformationService } from 'src/mbaas/core/service/SendInformation/send-information.service';
import { STATE_ERROR, GUARD_PRODUCTO, TRANSLATE, PAIS, MODULO, CANAL, LENGUAJE, ACTION_FROM_RETOMA } from 'src/mbaas/mbaas.const';
import { WorkflowService } from 'src/mbaas/core/service/workflow/workflow.service';
import { TranslateService } from '@ngx-translate/core';
import { MBAAS_STEPS } from 'src/mbaas/mbaas.route.const';
import { PARMS } from 'src/mbaas/mbaas.kind.const';
import { CatalogoService } from '../../core/service/catalogo/catalogo.service';

// {
//   "iss": "Online JWT Builder",
//   "iat": 1565906011,
//   "exp": 1597442011,
//   "aud": "www.example.com",
//   "sub": "jrocket@example.com",
//   "cliendtID": "0123456789",
//   "partner": "ML",
//   "product": "CAM",
//   "lenguaje": "es-CR",
//   "canal": "web",
//   "pais": "CR",
//   "modulo": "que???"
// }

@Component({
  selector: 'mbaas-mbaas-boot',
  templateUrl: './mbaas-boot.component.html',
  styleUrls: ['./mbaas-boot.component.scss'],
  providers: [CatalogoService]
})
export class MbaasBootComponent implements OnInit {

  public otp: string;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private auth: AuthService,
    private cripto: CriptoService,
    private workflow: WorkflowService,
    private obser: SendInformationService<any>,
    private translate: TranslateService,
    private catalogService: CatalogoService
  ) {
    this.translate.setDefaultLang(TRANSLATE.LENGUAJE);
    this.otp = this.route.snapshot.paramMap.get('otp');
  }

  ngOnInit() {
    this.auth.deleteAllTokenData();
    this.auth.authenticate(this.otp).subscribe(this.successAuthenticate(), this.errorAuthenticate());
  }

  successAuthenticate(): (response) => void {
    return resauth => {
      if (resauth.state === STATE_ERROR) {
        this.router.navigate(['/forbidden']);
        return;
      }
      const payload: any = {};
      const tokenDecoded = this.auth.decodeToken(resauth.access_token);
      console.log('tokenDecoded', tokenDecoded);
      this.cripto.getKeysRemote()
        .then(this.successGetKeyRemote(tokenDecoded, resauth, payload))
        .catch(this.errorAuthenticate());
    };
  }

  errorAuthenticate(): (err) => void {
    return err => {
      console.error('Authentication: ', err);
      this.router.navigate(['/forbidden']);
    };
  }

  successGetKeyRemote(tokenDecoded, resauth, payload): (mbaasKey) => void {
    return mbassKey => {
      this.cripto.keyCreator()
        .then(this.successKeyCreator(tokenDecoded, resauth, payload))
        .catch(this.errorAuthenticate());
    };
  }

  successKeyCreator(tokenDecoded, resauth, payload): (mbaasKey) => void {
    return (respFront: any) => {
      const productId = tokenDecoded.product;
      this.auth.saveTokenData(resauth, tokenDecoded.sub, this.saveLocalStorage(productId));
      payload.llaveEncriptacion = JSON.stringify(respFront);
      this.catalogService.data(PARMS).subscribe(result => {
        payload.parms = result[0];
        this.obser.sendData(true, GUARD_PRODUCTO);
        this.obser.sendData(this.callBackRetoma(), ACTION_FROM_RETOMA);
        this.workflow.workflow(MBAAS_STEPS.APPBOOT, payload);
      });
    };
  }

  saveLocalStorage(productId: string): boolean {
    const save = {
      Product_template: true
    }[productId];
    return (save === undefined) ? true : save;
  }

  callBackRetoma(): (close, data) => void {
    console.log('CLOSE: ', close);
    return (close, data) => {
      console.log('data: ', data);
      this.obser.sendData(data.pais, PAIS);
      this.obser.sendData(data.modulo, MODULO);
      this.obser.sendData(data.canal, CANAL);
      this.obser.sendData(data.lenguaje, LENGUAJE);
      const opcionRetoma = close() === 'Aceptar' ? 1 : 2;
      this.workflow.workflow(MBAAS_STEPS.APPBOOT, { opcionRetoma });
      close();
    };
  }

}
